import React from 'react';
import { Link as RouterLink, useParams } from 'react-router-dom';
import ReactApexChart from 'react-apexcharts';
import { merge } from 'lodash';
import { Icon } from '@iconify/react';
import PlusFill from '@iconify/icons-eva/plus-fill';
// material
import { useTheme } from '@mui/material/styles';
import {
  Box,
  Button,
  Card,
  CardContent,
  Container,
  FormControlLabel,
  Grid,
  Stack,
  Switch,
  Typography,
} from '@mui/material';
// hooks
import useSettings from '../hooks/useSettings';
// routes
import { PATH_DASHBOARD } from '../routes/paths';
// components
import Page from '../components/Page';
import HeaderBreadcrumbs from '../components/HeaderBreadcrumbs';
import Label from '../components/Label';
import BaseOptionChart from '../components/charts/BaseOptionChart';
import Steps from '../components/Steps';
import CampaignListTable from '../components/campaign/list/CampaignListTable';
import AnalyticsWeeklySales from '../components/AnalyticsWeeklySales';
import AnalyticsNewUsers from '../components/AnalyticsNewUsers';
import AnalyticsItemOrders from '../components/AnalyticsItemOrders';
import AnalyticsBugReports from '../components/AnalyticsBugReports';
import UserListTable from '../components/user/list/UserListTable';

const CampaignDetail = () => {
  const { themeStretch } = useSettings();
  const theme = useTheme();
  const { name } = useParams();

  const chartOptions = merge(BaseOptionChart(), {
    chart: { sparkline: { enabled: true } },
    legend: { show: false },
    plotOptions: {
      radialBar: {
        hollow: { size: '78%' },
        track: { margin: 0 },
        dataLabels: {
          name: { show: false },
          value: {
            offsetY: 6,
            color: theme.palette.common.black,
            fontSize: theme.typography.subtitle2.fontSize,
          },
        },
      },
    },
  });

  return (
    <Page title="Campaign: Detail Page">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading={name}
          links={[{ name: 'Campaign', href: PATH_DASHBOARD.campaign.list.all }, { name }]}
        />
        <Stack spacing={5}>
          <Card>
            <CardContent>
              <Stack spacing={3}>
                <Stack direction="row" justifyContent="space-between">
                  <Stack spacing={1}>
                    <Stack direction="row" spacing={2} alignItems="center">
                      <Typography variant="h6">{name}</Typography>
                      <Label variant={theme.palette.mode === 'light' ? 'ghost' : 'filled'} color="success">
                        Submission Running
                      </Label>
                    </Stack>
                    <Typography
                      variant="body2"
                      sx={{ opacity: 0.72 }}
                    >{`Created on ${new Date().toDateString()}`}</Typography>
                  </Stack>
                  <Stack direction="row" spacing={1} sx={{ height: 'fit-content' }}>
                    <FormControlLabel
                      labelPlacement="start"
                      control={<Switch />}
                      label={<Typography variant="subtitle2">Task On/Off</Typography>}
                    />
                    <Button variant="outlined" size="small">
                      Edit
                    </Button>
                    <Button
                      size="small"
                      variant="contained"
                      component={RouterLink}
                      to={PATH_DASHBOARD.campaign.newUserPost}
                      startIcon={<Icon icon={PlusFill} />}
                    >
                      Post User
                    </Button>
                    <Button
                      size="small"
                      variant="contained"
                      component={RouterLink}
                      to={PATH_DASHBOARD.campaign.create}
                      startIcon={<Icon icon={PlusFill} />}
                    >
                      Add Budget
                    </Button>
                  </Stack>
                </Stack>
                <Grid container>
                  <Grid item xs={12} md={4}>
                    <Stack direction="row" spacing={1} alignItems="center">
                      <ReactApexChart type="radialBar" series={[40]} options={chartOptions} width={86} height={86} />
                      <Typography variant="body2" sx={{ opacity: 0.72 }}>
                        $600 Budget Used
                      </Typography>
                    </Stack>
                  </Grid>
                  <Grid item xs={12} md={8}>
                    <Steps steps={['Setup', 'Approval', 'Scheduled', 'Open', 'Ended']} activeStep={0} />
                  </Grid>
                </Grid>
              </Stack>
            </CardContent>
          </Card>

          <Grid container spacing={2}>
            <Grid item xs={12} sm={6} md={3}>
              <AnalyticsWeeklySales />
            </Grid>
            <Grid item xs={12} sm={6} md={3}>
              <AnalyticsNewUsers />
            </Grid>
            <Grid item xs={12} sm={6} md={3}>
              <AnalyticsItemOrders />
            </Grid>
            <Grid item xs={12} sm={6} md={3}>
              <AnalyticsBugReports />
            </Grid>
          </Grid>

          <Box sx={{ height: 720 }}>
            <CampaignListTable />
          </Box>

          {/* <UserSubmissionsTable rows={rows} /> */}
          <UserListTable />
        </Stack>
      </Container>
    </Page>
  );
};

export default CampaignDetail;
